    padding-bottom:40px
}
.change_pass .login-panel{
    width:230px;
    margin:0
}
.change_pass dd{
    width:230px
}
.change_pass .btn-send{
    width:228px
}
.change_pass .note{
    width:calc(100% - 230px - 27px);
    font-size:12px;
    color:#666;
    line-height:17px;
    margin:40px 12px 0 15px
}
.btn-back.spark,.spark.btn-lay{
    -webkit-animation:sparkBack 0.5s ease;
    animation:sparkBack 0.5s ease
}
@-webkit-keyframes sparkBack{
    0%{
        background-color:#72BBEF;
        color:#1E1E1E
    }
    50%{
        background-color:#F8E71C;
        color:#fff
    }
    100%{
        background-color:#72BBEF;
        color:#1E1E1E
    }
}
@keyframes sparkBack{
