  * {
    margin: 0;
    padding: 0
  }
  
  html,
  body {
    height: 100%;
    font-family: Tahoma, Helvetica, sans-serif;
    color: #1e1e1e;
    font-size: 12px;
    line-height: 15px;
    background-color: #eee
  }
  
  a {
    text-decoration: none;
    color: #2789ce;
    outline: 0;
  }
  
  a:hover {
    text-decoration: underline;
    cursor: pointer
  }
  
  table {
    border-collapse: collapse
  }
  
  
  input,
  textarea {
    font-family: Tahoma, Helvetica, sans-serif;
    color: #1e1e1e;
    font-size: 12px;
    border: 0 #aaa solid;
    background: #fff;
    box-shadow: inset 0 1px 0 rgba(0, 0, 0, .5);
    border-radius: 4px;
    padding: 5px;
    margin: 0 5px 5px 0;
    box-sizing: border-box
  }
  
  input:focus,
  textarea:focus {
    padding: 4px
  }
  
  input.disable,
  textarea.disable {
    background-color: #dcdcdc;
    color: #666;
    box-shadow: none;
    border: 1px solid #bbb
  }
  
  label {
    cursor: pointer
  }
  
  select {
    width: 120px;
    height: 25px;
    line-height: 25px;
    cursor: pointer;
    margin: 0 5px 5px 0
  }
  
  option {
    padding: 3px
  }
  
  h2 {
    font-size: 13px;
    line-height: 20px;
    padding-top: 6px;
    margin-bottom: 6px
  }
  
  a img {
    border: 0 solid #fff
  }
  
  p {
    margin-bottom: 7px
  }
  
